import Day from "./Day";
import "./DayList.css";
import Bar from "./Bar";
import AddButton from "./AddButton";

function DayList({  date }) {
  
  return (
    <div className="background">
     
     <div className="main"><h2 >{date}</h2>
      
      <Day clock="11시"></Day>
      <Day clock="12시"></Day>
      <Day clock="13시"></Day>
      <Day clock="14시"></Day>
      <Day clock="15시"></Day>
      <Day clock="16시"></Day>
      <Day clock="17시"></Day>
      <Day clock="18시"></Day></div>
     
      <div className="add"><AddButton text="Add"></AddButton></div>
      
      <div className="nav-bar">
        <Bar></Bar>
      </div>

    </div>
  );
}

export default DayList;
