import React from "react";
import "./Day.css";
import Itembtn from "./Itembtn";

const Day = ({ clock }) => {
  const bab = [
    ["밥약 1", "11시"],
    ["밥약 2", "11시"],
    ["밥약 3", "11시"],
    ["밥약 1", "13시"],
    ["밥약 2", "13시"],
    ["밥약 1", "15시"],
  ];

  return (
    <div className="ground">
      {clock}
      {bab.map((text) => {
        if (text[1] === clock) return <Itembtn text={text[0]}></Itembtn>;
      })}
    </div>
  );
};

export default Day;
